import React, { useEffect, useState, useMemo } from "react";
import Pagination from "react-bootstrap/Pagination";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Spinner from "react-bootstrap/Spinner";
import Modal from "react-bootstrap/Modal";
import { useAuth } from "../contexts/AuthContext";
import { toast } from "react-toastify";

const GROUP_CODE_FIELDS = [
  { key: "groupId", label: "Group ID" },
  { key: "groupCode", label: "Group Code" },
  { key: "line", label: "Line" },
  { key: "lineCode", label: "Line Code" },
  { key: "opsCode", label: "Ops Code" },
  { key: "inlineLogo", label: "Inline Logo" },
  { key: "bulletLogo", label: "Bullet Logo" },
  { key: "backLogo", label: "Back Logo" },
  { key: "gateLogo", label: "Gate Logo" },
  { key: "firstNumber", label: "First Number" },
  { key: "lastNumber", label: "Last Number" },
  { key: "defaultLanguage", label: "Default Language" },
  { key: "siteId", label: "Site ID" },
];

function compare(a, b, key, direction) {
  if (a[key] == null && b[key] == null) return 0;
  if (a[key] == null) return 1;
  if (b[key] == null) return -1;
  if (typeof a[key] === "string") {
    return direction === "asc"
      ? a[key].localeCompare(b[key])
      : b[key].localeCompare(a[key]);
  }
  return direction === "asc" ? a[key] - b[key] : b[key] - a[key];
}

const GroupCodeCard = ({ group, onSave }) => {
  const [show, setShow] = useState(false);
  return (
    <>
      <Card
        className="mb-3 shadow-sm group-card-mini text-truncate"
        style={{ cursor: "pointer", minWidth: 0, maxWidth: 260, width: "100%" }}
        onClick={() => setShow(true)}
      >
        <Card.Body className="p-2 d-flex flex-column align-items-start justify-content-between">
          <div className="mb-1 text-truncate" style={{ width: "100%" }}><strong>ID:</strong> {group.groupId}</div>
          <div className="mb-1 text-truncate" style={{ width: "100%" }}><strong>Code:</strong> {group.groupCode}</div>
          <div className="mb-2 text-truncate" style={{ width: "100%", fontSize: "0.85em", color: "#888" }}><strong>Line:</strong> {group.line}</div>
          <Button variant="outline-primary" size="sm" className="align-self-end" onClick={e => { e.stopPropagation(); setShow(true); }}>Details</Button>
        </Card.Body>
      </Card>
      <GroupCodeDetailsModal show={show} onHide={() => setShow(false)} group={group} onSave={onSave} />
    </>
  );
};

function GroupCodeDetailsModal({ show, onHide, group, onSave, mode = "edit" }) {
  const [form, setForm] = useState(group || {});
  const [saving, setSaving] = useState(false);
  const { authenticatedFetch } = useAuth();

  useEffect(() => { setForm(group || {}); }, [group, show]);

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    setForm((f) => ({
      ...f,
      [name]: type === "checkbox" ? checked : value,
    }));
  };

  const handleSave = async () => {
    setSaving(true);
    try {
      const baseUrl = import.meta.env.VITE_API_BASE_URL || "";
      let resp, updated;
      resp = await authenticatedFetch(`${baseUrl}/api/GroupCodes`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(form),
      });
      if (!resp.ok) throw new Error("Failed to save group code");
      updated = await resp.json();
      toast.success("Group code updated");
      onSave(updated);
      onHide();
    } catch (e) {
      toast.error(e.message || "Error saving group code");
    } finally {
      setSaving(false);
    }
  };

  return (
    <Modal show={show} onHide={onHide} size="lg" centered contentClassName="group-modal-responsive">
      <Modal.Header closeButton>
        <Modal.Title>Group Code Details</Modal.Title>
      </Modal.Header>
      <Modal.Body style={{ maxHeight: '60vh', overflowY: 'auto', minHeight: 200 }}>
        <Form>
          {GROUP_CODE_FIELDS.map(({ key, label }) => (
            <Form.Group key={key} className="mb-3" as={Row}>
              <Form.Label column sm={4} xs={5} style={{ fontWeight: 500 }}>{label}</Form.Label>
              <Col sm={8} xs={7}>
                <Form.Control
                  name={key}
                  value={form[key] ?? ""}
                  onChange={handleChange}
                  disabled={saving || key === "groupId"}
                  readOnly={key === "groupId"}
                  type={typeof form[key] === "number" ? "number" : "text"}
                />
              </Col>
            </Form.Group>
          ))}
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="primary" onClick={handleSave} disabled={saving}>
          {saving ? <Spinner size="sm" animation="border" /> : "Save"}
        </Button>
        <Button variant="secondary" onClick={onHide} disabled={saving}>Cancel</Button>
      </Modal.Footer>
    </Modal>
  );
}

const GroupCodesScreen = () => {
  const { user, authenticatedFetch } = useAuth();
  const [groups, setGroups] = useState([]);
  const [loading, setLoading] = useState(true);
  const [sort, setSort] = useState({ key: "groupId", direction: "asc" });
  const [filter, setFilter] = useState({ line: "", groupCode: "" });
  const [page, setPage] = useState(1);
  const pageSize = 12;
  const [totalCount, setTotalCount] = useState(0);

  useEffect(() => {
    setLoading(true);
    const baseUrl = import.meta.env.VITE_API_BASE_URL || "";
    const params = new URLSearchParams();
    params.append("skip", (page - 1) * pageSize);
    params.append("top", pageSize);
    if (filter.line) params.append("line", filter.line);
    if (filter.groupCode) params.append("groupCode", filter.groupCode);
    authenticatedFetch(`${baseUrl}/api/GroupCodes?${params.toString()}`)
      .then(async (r) => {
        if (!r.ok) throw new Error("Failed to fetch group codes");
        const total = r.headers.get("x-total-items");
        const data = await r.json();
        setGroups(Array.isArray(data) ? data : (data.data || []));
        setTotalCount(total ? parseInt(total, 10) : (Array.isArray(data) ? data.length : (data.totalCount || (data.data ? data.data.length : 0))));
      })
      .catch((e) => toast.error(e.message || "Error loading group codes"))
      .finally(() => setLoading(false));
  }, [authenticatedFetch, page, pageSize, filter.line, filter.groupCode]);

  // No client-side filtering/sorting; server handles it
  const sorted = groups;
  const totalPages = Math.max(1, Math.ceil(totalCount / pageSize));
  const paged = sorted;

  useEffect(() => {
    // Reset to first page if filter or sort changes
    setPage(1);
  }, [filter, sort]);

  const handleSave = (updated) => {
    setGroups((gs) => gs.map((g) => (g.groupId === updated.groupId ? updated : g)));
  };

  if (!user || !(user.roles?.includes("admin"))) {
    return <div className="mt-4 text-danger">Access denied.</div>;
  }

  return (
    <div className="container mt-4">
      <h2 className="mb-3">Group Codes</h2>
      <Form className="mb-3">
        <Row className="g-2">
          <Col xs={12} sm={6} md={4} lg={3} xl={2}>
            <Form.Control
              size="sm"
              placeholder="Filter by Group Code"
              value={filter.groupCode}
              onChange={e => setFilter(f => ({ ...f, groupCode: e.target.value }))}
            />
          </Col>
          <Col xs={12} sm={6} md={4} lg={3} xl={2}>
            <Form.Control
              size="sm"
              placeholder="Filter by Line"
              value={filter.line}
              onChange={e => setFilter(f => ({ ...f, line: e.target.value }))}
            />
          </Col>
        </Row>
      </Form>
      {loading ? (
        <div className="text-center my-5">
          <Spinner animation="border" />
        </div>
      ) : (
        <>
          <Row className="g-2">
            {paged.map((group) => (
              <Col key={group.groupId} xs={12} sm={6} md={4} lg={3} xl={2} xxl={2}>
                <GroupCodeCard group={group} onSave={handleSave} />
              </Col>
            ))}
          </Row>
          {totalPages > 1 && (
            <div className="d-flex justify-content-center mt-3">
              <Pagination>
                <Pagination.First onClick={() => setPage(1)} disabled={page === 1} />
                <Pagination.Prev onClick={() => setPage(p => Math.max(1, p - 1))} disabled={page === 1} />
                {/* Show first page */}
                <Pagination.Item active={page === 1} onClick={() => setPage(1)}>{1}</Pagination.Item>
                {/* Show ellipsis if needed before current page */}
                {page > 4 && <Pagination.Ellipsis disabled />} 
                {/* Show up to 2 pages before current */}
                {Array.from({length: 2}, (_, i) => page - 2 + i).filter(p => p > 1 && p < page).map(p => (
                  <Pagination.Item key={p} onClick={() => setPage(p)}>{p}</Pagination.Item>
                ))}
                {/* Show current page if not first/last */}
                {page !== 1 && page !== totalPages && (
                  <Pagination.Item active>{page}</Pagination.Item>
                )}
                {/* Show up to 2 pages after current */}
                {Array.from({length: 2}, (_, i) => page + 1 + i).filter(p => p < totalPages && p > page).map(p => (
                  <Pagination.Item key={p} onClick={() => setPage(p)}>{p}</Pagination.Item>
                ))}
                {/* Show ellipsis if needed after current page */}
                {page < totalPages - 3 && <Pagination.Ellipsis disabled />}
                {/* Show last page if more than one */}
                {totalPages > 1 && (
                  <Pagination.Item active={page === totalPages} onClick={() => setPage(totalPages)}>{totalPages}</Pagination.Item>
                )}
                <Pagination.Next onClick={() => setPage(p => Math.min(totalPages, p + 1))} disabled={page === totalPages} />
                <Pagination.Last onClick={() => setPage(totalPages)} disabled={page === totalPages} />
              </Pagination>
            </div>
          )}
        </>
      )}
      {sorted.length === 0 && !loading && <div>No group codes found.</div>}
    </div>
  );
};

export default GroupCodesScreen;
